export default {
	props: ['cid', 'initialize'],
	data() {
		return {
			banner: null,
			dataSource: null
		};
	},
	watch: {
		initialize(value, before) {
			if (value && !this.banner) {
				this.loadData();
			}
		}
	},
	methods: {
		loadData() {
			this.banner = true;
			console.log(this.cid);
		}
	}
}
